import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom'
import axios from 'axios';

import Layout from '../components/Layout';
import hash from "../../hash";
import Navbar from '../components/Navbar';

import logo from '../../assets/spotify.svg';
import headshot from '../../assets/headshot.jpg';
import albumcover from '../../assets/albumcover.jpg';

import topArtists from './albums.json';

import Player from '../../Player';
import { brotliDecompressSync } from 'zlib';

var topTracks1 = [];
var topArtists1 = [];
var allTracks1 = [];
var name1 = "";
var email1 = "";


const Home = () => {
  const [token, setToken] = useState(null);

  const [topTracks, setTopTracks] = useState([]);
  const [topArtists, setTopArtists] = useState([]);
  const [allTracks, setAllTracks] = useState([]);
  const [allAlbums, setAllAlbums] = useState([]);
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [post, setPost] = useState({});

  const [albums, setAlbums] = useState([]);


  useEffect(() => {
    // Set token
    let _token = hash.access_token;

    if (_token) {
      setToken(_token)
      getTopTracks(_token)
      getTopArtists(_token);
      getAllTracks(_token, 'https://api.spotify.com/v1/me/tracks?limit=50&offset=0');
      getAllAlbums(_token, 'https://api.spotify.com/v1/me/albums?limit=50&offset=0');
      getNameAndEmail(_token);

      function removeDuplicates(array) {
        return array.filter((a, b) => array.indexOf(a) === b)
      };

      setTimeout(() => {
        console.log('BAM')
        const post = {
          name: name1,
          email: email1,
          artists: JSON.stringify(topArtists1),
          topTracks: JSON.stringify(topTracks1),
          allTracks: JSON.stringify(allTracks1)
        }
        console.log(post);
        axios
          .post('http://us-central1-musical-buddies.cloudfunctions.net/api/newUser',
            post,
            {
              headers: { 'Authorization': 'Bearer ' + token },
            }
          ).then(res => console.log('sent!', res))
          .catch(err => console.log(err))
      }, 5000)
    }
  }, []);

  // Get Top Tracks
  const getTopTracks = (token) => {
    fetch('https://api.spotify.com/v1/me/top/tracks?limit=50', {
      headers: { 'Authorization': 'Bearer ' + token }
    }).then(response => response.json())
      .then(data => {
        var tracks = data.items.map(x => [x.name, x.id, x.popularity])
        topTracks1 = [...topTracks1, ...tracks]
        // console.log(topTracks1);
        setTopTracks(tracks);
        // console.log(tracks);
        // console.log(topTracks);
        setPost({ ...post, topTracks: tracks });
        // console.log(post);
      })
  }

  // Get Top Artists
  const getTopArtists = (token) => {
    fetch('https://api.spotify.com/v1/me/top/artists?limit=50', {
      headers: { 'Authorization': 'Bearer ' + token }
    }).then(response => response.json())
      .then(data => {
        var artists = data.items.map(x => [x.name, x.id, x.popularity])
        setTopArtists(artists);
        topArtists1 = [...topArtists1, ...artists];
        // console.log(artists);
      })
  }

  // Get All Tracks 
  const getAllTracks = (token, url) => {
    // fetch('https://api.spotify.com/v1/me/tracks?limit=50&offset=0', {
    fetch(url, {
      headers: { 'Authorization': 'Bearer ' + token }
    }).then(response => response.json())
      .then(data => {
        // console.log(data);
        if (!!data.items) {
          var tracks = data.items.map(x => [x.track.name, x.track.id, x.track.popularity]);
          var newAllTracks = [...allTracks, ...tracks]
          setAllTracks(newAllTracks);
          allTracks1 = [...allTracks1, ...tracks];
        }

        if (!!data.next) {
          getAllTracks(token, data.next)
        }
        // console.log(allTracks);
      })
  }

  // Get All Albums
  const getAllAlbums = (token, url) => {
    // fetch('https://api.spotify.com/v1/me/albums?limit=50&offset=0', {
    var albumIDs;
    fetch(url, {
      headers: { 'Authorization': 'Bearer ' + token }
    }).then(response => response.json())
      .then(data => {
        // console.log('DATA')
        // console.log(data);
        if (!!data.items) {
          albumIDs = data.items.map(x => [x.album.id]);
          // console.log(albumIDs)
        }

        const getAllTracksFromAlbum = (token, tracks = []) => {
          if (!!tracks) {
            // trks = tracks.map(x => x.id);
            // console.log('after passing')
            // console.log(tracks);
            fetch('https://api.spotify.com/v1/tracks?ids=' + tracks.toString(), {
              headers: { 'Authorization': 'Bearer ' + token }
            }).then(response => response.json())
              .then(data => {
                // console.log(data)
                if (!!data.tracks) {
                  var trackFinal = data.tracks.map(x => [x.name, x.id, x.popularity])
                  // console.log(trackFinal);
                  var newAllTracks = [...allTracks, ...trackFinal]
                  setAllTracks(newAllTracks);
                  allTracks1 = [...allTracks1, ...trackFinal]
                  // console.log(allTracks);
                }
              })
          }
        }

        for (var i = 0; i < albumIDs.length; i++) {
          fetch(`https://api.spotify.com/v1/albums/${albumIDs[i]}/tracks?limit=50`, {
            headers: { 'Authorization': 'Bearer ' + token }
          }).then(response => response.json())
            .then(data => {
              // console.log('Grabbing album tracks...')
              // console.log(data);
              if (!!data.items) {
                var trackIDs = data.items.map(x => x.id);
                // console.log('Before passing..')
                // console.log(trackIDs);
                getAllTracksFromAlbum(token, trackIDs);
              }
            })
        }

        if (!!data.next) {
          getAllAlbums(token, data.next)
        }
      })
  }


  // Get Name & Email
  const getNameAndEmail = (token) => {
    console.log('Getting name and email...')
    fetch('https://api.spotify.com/v1/me', {
      headers: { 'Authorization': 'Bearer ' + token }
    }).then(response => response.json())
      .then(data => {
        setName(data.display_name);
        setEmail(data.email);
        name1 = data.display_name;
        email1 = data.email;
      })
  }

  const getTopAlbums = (token) => {
    fetch('https://api.spotify.com/v1/me/top/artists?limit=6', {
      headers: { 'Authorization': 'Bearer ' + token }
    }).then(response => {
      return response.json();
    })
      .then(data => {
        setAlbums(data.items)
      })
  }

  const displayAlbums = albums.map((artist) => {
    return (
      <a key={artist.id} href={artist.external_urls.spotify} target="_blank" rel="noopener noreferrer">
        <img src={artist.images[1].url} style={{ maxHeight: '250px' }} className="pr-3 pb-3" />
      </a>
    )
  })

  return (
    <Layout>
      <div style={{ backgroundColor: '#191414' }}>
        <Navbar />
        <div className="d-flex justify-content-center text-white">
          <div className="w-100 row">
            <div className="col-1" />
            <div className="col-5">
              <h4>Your Top</h4>
              <h1>MATCHES</h1>
              <div>
                {[1, 2, 3, 4, 5, 6].map(() => {
                  return (
                    <Link to="/profile">
                      <img src={headshot} style={{ maxHeight: '250px' }} className="pr-3 pb-3" />
                    </Link>
                  )
                })}
              </div>
            </div>
            <div className="col-5">
              <h4>Your Top</h4>
              <h1>ARTISTS</h1>
              <div>You are 89% unique!</div>
              <div>
                {/* {[1, 2, 3, 4, 5, 6].map(() => {
                  return (
                    <img src={albumcover} style={{ maxHeight: '250px' }} className="pr-3 pb-3" />
                  )
                })} */}
                {albums &&

                  displayAlbums
                  // albums.map((artist) => {
                  //   return (
                  //     <a key={artist.id} href={artist.external_urls.spotify}>
                  //       <img src={artist.images[1].url} style={{ maxHeight: '250px' }} className="pr-3 pb-3" />
                  //     </a>
                  //   )
                  // })
                }
              </div>

            </div>
            <div className="col-1" />
          </div>
        </div>
      </div>
    </Layout>
  )
}

export default Home;


